.nav{
    position: fixed;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.5rem 4.1vw;
	top: 0;
	right: 0;
	left: 0;
	z-index: 2;
	transition: .3s;
}

.nav.active {
	/* From https://css.glass */
background: rgba(255, 255, 255, 0.2);

box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(5px) !important ;
-webkit-backdrop-filter: blur(5px) !important;
	padding-top: 10px !important;
	padding-bottom: 10px !important;
	box-shadow: 0 -7px 24px -15px #000;
}

.header-logo{
    height:3rem;
    width: 4rem;
    z-index: 100 !important;
}

.header-logo1{
    height:2.5rem;
    width: 4rem;
    z-index: 100 !important;
}

.header-left{
    display: flex;
    cursor: pointer;
    align-items: center;
    gap: 4px;
    justify-content: center;
}

.active-link {
    color: rgba(255, 0, 0, 0.664) !important;
}

.header-left p{
    font-size: larger;
    
}   

.product {
    position: relative;
}

.dropdown-content {
    width: 100%;
    height: auto;
    padding: 2rem 0rem;
    background: rgba(3, 3, 3, 0.2);
    z-index: -1;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px) !important;
    -webkit-backdrop-filter: blur(5px) !important;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    position: absolute;
    top: 15px;
    opacity: 0;  /* Initial opacity set to 0 */
    transition: opacity 0.3s ease-in-out;  /* Add transition for smooth animation */
}

.product:hover .dropdown-content {
    opacity: 1;  /* Change opacity to 1 on hover */
}

.dropdown-content a {
    color: white !important;
    padding: 5px;
    transition: background-color 0.3s ease-in-out;  /* Add transition for smooth background color change */
}

.dropdown-content a:hover {
    background-color: rgba(49, 49, 49, 0.753);
}

.navLinks{
    color: rgba(214, 211, 211, 0.918) !important;
}
.navLinks.active{
    color: rgba(0, 0, 0, 0.918) !important;
}

.sub-menu.active{
    color: rgba(0, 0, 0, 0.918) !important;
}

.header-right{
    display: flex;
    gap: 1rem;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
}

.header-right a{
    color: #000;
}

a{
    text-decoration: none;
}

.sub-menu{
    display: none;
    font-size: 2rem;
    color: white;
}

.responsive-nav{
    position: absolute;
    top: -500%;
    width: 100%;
    min-height: 100vh;
    background-color: #f2f2f2;
    opacity: 0;
    z-index: 5;
    transition: all cubic-bezier(0.19, 1, 0.22, 1)1s;
    
}

.res-close{
    position: absolute;
    top: 2%;
    right: 2%;
    font-size: 3rem;
}



.res-navLinks{
    
}

.res-dropdown-content{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 1.5rem .5rem;
}

/* ... Your existing CSS ... */

@media (max-width: 600px) {
    .sub-menu {
        display: block;
    }

    .navLinks {
        display: none;
    }

    .nav{
        position: fixed;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.5rem 4.1vw;
        
        top: 0;
        right: 0;
        left: 0;
        z-index: 2;
        transition: .3s;
    }
    

    .nav.active {
        
    }

    .navLinks.active {
        color: rgba(0, 0, 0, 0.918) !important;
    }

    .header-right {
        flex-direction: column; /* Stack items vertically */
        align-items: flex-end; /* Align items to the right */
        gap: 1rem;
    }

    .header-right a {
        color: white; /* Adjust color for better visibility */
    }

    .sub-menu {
        display: block;
        font-size: 2rem;
    }

    .dropdown-content {
        top: 40px; /* Adjust top position to avoid overlapping */
    }

    .dropdown-content a {
        color: white !important;
    }

    .responsive-nav{
        
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
    }

    .responsive-nav.active {
        position: fixed;
        top: 0;
        opacity: 1;
      }
      

    .responsive-nav a{
        text-decoration: none;
    color: black;
    transition: color 0.3s ease;
    cursor: pointer;
    font-size: small;
    text-transform: uppercase;

    }

    .responsive-nav a:hover {
        color: white;
    }

    .header-logo1-responsive{
        
            height:10rem;
            width: 10rem;
            z-index: 100;
      
    }

    
}
