.home-container{
    height: 100%;
    width: 100%;
}

.front-page {
    background-image: url('../../imgs/back1.jpg');
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    
    flex-direction: column;
    overflow-x: hidden;
    flex-shrink: 0;
}

.front-top{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    
}




.front-bottom{
    width: 100%;
    height: 45vh;
    display: flex;
    
    padding: .5rem;
    gap: .5rem;
    background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.35),
        rgba(0, 0, 0, 0.6)
    );
    
}

.h-img {
    height: 100%;
    object-fit: cover;
    min-width: 32.7%; /* Set the fixed width */
    max-height: 100%; /* Maintain aspect ratio */
}

.front-page h1{
        text-align: center;
        font-weight: 300;
    font-size: 3.5rem;
    letter-spacing: 4px;
    color:  #2C3E50    ;
}



  .box:hover .each-project-title {
    opacity: 1; /* Show the title on hover */
  }


.urban-logo{
	display: flex;
	gap: 2rem;
	
}

.home-moonkirilogo{
	height: 12rem;
	width: 50rem;
	transform: translateY(-10%);
}

.urban-logo img{
	height: 10rem;
	transform: translateY(-10%);
}

.front-page p{
    font-size: 2.5rem;
    font-family: 'Island Moments';
    letter-spacing: 1px;
    color: black;
}


.projects-container{
    height: 100%;
    width: 100%;
    
    background-color: #f2f2f2;
    padding: 120px 5vw;
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.projects-container h1{
    font-size: 3rem;
    font-family: var(--font-monSemi);
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 2%;
    font-weight: bolder;
}

.project-categories {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: center;
    justify-content: center;
    width: 50vw;
    transform: translateX(40%);
}

.project-categories a {
    white-space: nowrap;
    text-decoration: none;
    color: black !important;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
}

.project-categories a.active {
    color: red;
}

.project-main {
    margin-top: 2%;
    width: 75vw;
    transform: translateX(10%);
    height: 100%;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(400px, auto);
    grid-auto-flow: dense;
    transition: all 0.3s ease;
}

.box {
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
	
    display: flex;
    justify-content: center;
    /* align-items: end; */
    /* Add transition for smooth animation */
}

.each-project-title {
    position: absolute;
	margin-top: 1rem;
	text-align: center;
    font-family: var(--font-monExtra);
	text-decoration: underline;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 1.2rem;
    font-weight: bolder;
    color: white;
    z-index: 100;
    opacity: 0; /* Initially hide the title */
    transition: opacity 0.3s ease; /* Add transition for smooth animation */
  }
  
  .box:hover .each-project-title {
    opacity: 1; /* Show the title on hover */
  }

 

.box.viewproject {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
    

    /* Add transition for smooth animation */
}


.box img {
    width: 100%;
    height: 100%;
	
    object-fit: cover;
    object-position: center;
}

.content {
	max-width: 1920px;
	width: 100%;
	padding: 0 5vw;
	margin: 0 auto;
}

.section-content {
	width: 100%;
	padding: 0 11.7292vw;
}

/************************/
.section-title {
	width: 100%;
	margin-bottom: 150px;
}

.section-title__wrapp {
	position: relative;
	padding-top: 110px;

	font-size: 6.511vw;
	text-transform: uppercase;
}

.section-title__num {
	position: absolute;
	top: 0;
	right: 10px;
	font-size: var(--font-size-num);
	font-style: italic;
	line-height: .8;
}

.section-title__item {
	font-size: 3rem;
    font-family: var(--font-monSemi);
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: bolder;
}

.section-title__item,
.section-title__num {
	font-family: var(--font-monSemi);
}

/******** section-top-qualities ********/
.top__wrapp {
	display: flex;
	justify-content: space-between;
	width: 100%;
    margin-top: -8%;
}

.top__txt {
	width: 45%;
}

.top__item {
	width: 100%;
	margin-bottom: 60px;
	cursor: pointer;
	user-select: none;
}

.top__item.active {
	pointer-events: none;
}

.top{
    margin-top: -5%;
}

.top__head h3,
.top__head h3 span {
	line-height: 1;
	font-size: 2rem;
	font-family: var(--font-monSemi);
}

.top__head_hover,
.top__head_actve {
	transition: .3s;
}

.top__item:hover .top__head_hover,
.top__item.active .top__head_hover,
.top__item.active .top__head_actve {
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.top__descript {
	display: none;
	height: auto;
	padding-top: 30px;
}

.top__descript p {
	text-transform: uppercase;
	font-size: 1rem;
	line-height: var(--line-height-txt);
}

/*****************/
.top__img {
	position: relative;
	width: 45%;
	height: 650px;
	overflow: hidden;
}

.top__img-line {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.top__img-line:before,
.top__img-line:after {
	position: absolute;
	content: '';
	width: 1000%;
	height: 2px;
	background: #000;
	top: 50%;
	left: 50%;
}

.top__img-line:before {
	transform: translate(-50%, -50%) rotate(49.5deg);
}

.top__img-line:after {
	transform: translate(-50%, -50%) rotate(-49.5deg);
}

.top__img-wrapp {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border: 2px solid #000;
	z-index: 1;
}

.top__img-item {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	overflow: hidden;
	transform: scaleY(.4);
	transition: .8s cubic-bezier(.9, 0, .1, 1);
}

.top__img-item.active {
	opacity: 1;
	transform: scaleY(1);
}

.top__img-item img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.top{
    background-color: #f2f2f2;
}

/******** section-get-in-touch ********/
.getin__wrapp {
	padding-bottom: 100px;
    margin-top: -8%;
}

.getin__item {
	list-style: none;
}

.getin__item+.getin__item {
	margin-top: 30px;
}

.getin__link {
	position: relative;
	display: inline-block;
	padding-right: 170px;
	font-family: var(--font-monExtra);
	font-size: 1.8rem;
	color: #000;
	transition: .3s;
    font-weight: 100;
}

.getin__link:hover {
	padding-right: 180px;
	color: transparent;
	-webkit-text-stroke: 1px #000;
}

.getin__link:after {
	position: absolute;
	content: '';
	width: 90px;
	height: 35px;
	top: 50%;
	right: 0;
	transform: translateY(-50%);
	background-image: url(../../../src/img/arrov-link.png);
	background-repeat: no-repeat;
	background-size: 100% 100%;
}

.getin{
    background-color: #f2f2f2;
}

@media (max-width: 600px) {
    
    .front-page {
        align-items: center;
        justify-content: center;
        padding: 0rem 5vw;
        
    }

    .front-bottom {
        display: none;
    }

    .front-page h1 {
        font-size: 2.5rem;
    }

    .front-page p {
        font-size: 1.8rem;
    }

    .project-main {
        
        grid-template-columns: repeat(1, 1fr);
        grid-auto-rows: minmax(300px, auto);
      
    }

    .top__head_hover{
        font-size: .5rem;
    }

    .getin{
            padding-top: 4rem;
    }
}

