.product-display-container{
    width: 100%;
    height: 100%;
    background-color: #f2f2f2;
}

.pd-top{
    position: relative;
    background-repeat: no-repeat;
    background-size: cover; /* Ensure the image covers the entire container */
    background-position: center center; 
    width: 100%;
    height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.pd-top h1{
    font-size: 3rem;
    font-family: var(--font-monSemi);
    letter-spacing: 1px;
    text-transform: capitalize;
    
    color: rgb(248, 248, 248);
    margin-bottom: 2%;
    font-weight: bolder;
}

.pd-top p{
    font-size: 2.5rem;
    
    font-weight: 900;
    letter-spacing: 1px;
    color: rgb(99, 15, 15);
}

.pd-bottom{
    padding: 0 5vw;
    padding-bottom: 100px;
}

/* shop css */

.shopTitles {
    margin-top: 100px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    font-family: var(--font-monSemi);
    font-size: 2rem;
    margin-bottom: 3%;
  }
  .shop-logo{
    height: 10rem;
  }
  .productss {
    width: 100%;
    height: auto;
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: center;
  }
  
  .products {
    border-radius: 15px;
    width: 220px;
    height: 350px;
    margin: 100px;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .products img {
    width: 400px;
    height: 400px;
    object-fit: cover;
  }
  
  .products .descriptions {
    text-align: center;
  }
  
  .products:hover {
    transition: 0.3s ease-in;
    cursor: pointer;
  }
  
  .addToCartBttns {
    background-color: transparent;
    border: 2px solid rgb(19, 19, 19);
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
  
  .addToCartBttns:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
  }
  
  @media (max-width: 600px) {
    .pd-top {
        height: 40vh; /* Adjusted height for smaller screens */
    }

    .pd-top h1 {
        font-size: 2rem; /* Adjusted font size for smaller screens */
    }

    .pd-top p {
        font-size: 1.5rem; /* Adjusted font size for smaller screens */
    }

    .pd-bottom {
        padding-bottom: 50px; /* Adjusted padding for smaller screens */
        padding-right: 3rem;
    }

    .shop-logo{
      height: 5rem;
    }

    .productss {
        margin-right: 10rem;
        grid-template-columns: 1fr; /* Single column for smaller screens */
    }

    .products {
        border-radius: 15px;
        width: 200px;
        height: 350px;
        padding-right: 2.5rem !important;
        display: flex;
        
      }
      
      .products img {
        width: 300px;
      }
}